$(".partners .swiper-slide").click(function () {
    let activeTab = $(this).attr("data-tabs-path")
    setActivePartner(activeTab)
})


function setActivePartner(activeTab) {
    $(".partners__content").removeClass("partners__content--active")
    $(`.partners__content[data-tabs-path="${activeTab}"]`).addClass("partners__content--active")
}


$(".partners .swiper-button-prev").click(function () {
    let activeTab = +$(".partners__content--active").attr("data-tabs-path")
    let slidesCount = $(".partners .swiper-slide:not(.swiper-slide-duplicate)").length
    if(activeTab>1){
        setActivePartner(activeTab-1)
    } else{
        setActivePartner(slidesCount)
    }
})

$(".partners .swiper-button-next").click(function () {
    let activeTab = +$(".partners__content--active").attr("data-tabs-path")
    let slidesCount = $(".partners .swiper-slide:not(.swiper-slide-duplicate)").length
    if(activeTab<slidesCount){
        setActivePartner(activeTab+1)
    } else{
        setActivePartner(1)
    }
})
