$(".nav__item").hover(onIn, onOut);
$(".nav__item-show").click(function() {
    if ((window.innerWidth < 1200)) {
        $(this).parents(".nav__item").toggleClass("nav__item--active")
        $(this).siblings(".nav__item-hidden").slideToggle()
    }
})

function onIn() {
    if (window.innerWidth > 1200) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                el.addClass("nav__item--active")
            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 1200) {
        $(this).removeClass("nav__item--active")
    }
}


$(".header__search-toggle").click(function () {
    $("body").addClass("fixed-body")
    $(".header__search").addClass("header__search--active")
})

$(".header__search-bg,.header__search-close").click(function () {
    $(".header__search").removeClass("header__search--active")
    $("body").removeClass("fixed-body")
})

$(".header__burger").click(function () {
    $("body").toggleClass("fixed-body")
    $(".header").toggleClass("header--active")
})

if (window.innerWidth < 1200){
    $(".header-catalog__title").click(function () {
        $(this).toggleClass("header-catalog__title--active")
        $(this).siblings(".header-catalog__links").slideToggle()
    })
}
